import React from 'react';
 
import { NavLink } from 'react-router-dom';
import './Navigation.css'
 
const Navigation = () => {
    return (
       <div className='Navigation'>
          <NavLink className='Navigation-NavLink' to="/react-portfolio">Home</NavLink>
          <NavLink className='Navigation-NavLink' to="/about">About</NavLink>
          <NavLink className='Navigation-NavLink' to="/portfolio">Portfolio</NavLink>
          <NavLink className='Navigation-NavLink' to="/group">Group Projects</NavLink>
          <NavLink className='Navigation-NavLink' to="/contact">Contact</NavLink>
          <NavLink className='Navigation-NavLink' to="/resume">Resume</NavLink>
       </div>
    );
}

export default Navigation;
