import React from 'react';
import Header from '../Header/Header'; 
import Navigation from '../Navigation/Navigation';
import Hicks from '../img/Hicks.jpg';
import Footer from '../footer/Footer';
import Bar from '../bar/Bar';
import './About.css';
import {Container, Row, Col} from 'reactstrap';
import ReactRoundedImage from 'react-rounded-image';

const about = () => {
    return (
      
       <div>
       <Header />
       <div>
       <Row>
           <Navigation />
       </Row>
       <Row>
         <h2 className='About-h2'>About Me</h2>
       </Row> 
       <Row>
           <Bar
           color='darkblue'
           />
       </Row>  
            <Container> 
                <Row>
                    <Col className='About-column'>
                    <ReactRoundedImage className='About-img' 
                    image={Hicks} alt="portrait"
                    roundedColor="darkblue"
                    imageWidth="400"
                    imageHeight="400"
                    roundedSize="13"
                    hoverColor="tan"
                    />
                    <p className="About-text">
                    After a succesful career in the field of education, I have decided to pursue a career in computer programing.  After seeing my first through fifth grade students create programs through the Tynker Program, I knew it was time for me to fulfill my desire to become a computer programmer.  With one hundred percent certainty that this was the path I wanted to take, I enrolled in the Coding Bootcamp Program offered by Case Western Reserve University. The program is very challenging, but I'm learning a ton!
                    </p>
                    </Col>
                    <Col className='About-column'>
                    <p className="About-text">Thus far I have gained knowledge, practice and experience with using HTML, CSS, Javascript, JQuery, Handlebars, Node, Express, PWA, React, MySQL and Mongodb programming languages. I have highlighted a few of my creations on my Profile page and you will find my group projects on my Group Projects page.  My github repository can be found at <a class="github" href="https://github.com/Kathicks387?tab=repositories">https://github.com/Kathicks387.</a> After obtaining my Master's Degree in education and successfully teaching others to learn, I hold the belief that education is a life long journey.  Although I have completed the Case Western Reserve University Bootcamp Course, I know I have years of learning to look forward to.  Computer programming changes on a daily basis and I am ready an excited about learning all that is to come.  I will eagerly await the opportunity to apply all I have learned and will learn in the future.                  
                    </p>
                    </Col>
                    </Row>
                   
                </Container>
                <div>
                <Footer />
                </div>
               
                </div>   
                </div>
      )
};

 
export default about;