import React from 'react';
import Header from '../Header/Header';
import Navigation from '../Navigation/Navigation';
import Hicks from '../img/Hicks.jpg';
import Footer from '../footer/Footer';
import Bar from '../bar/Bar';
import './Contact.css';
import { Row } from 'reactstrap';


import {Card, CardText, CardBody, CardTitle, CardLink, CardImg} from 'reactstrap';


const Contact = () => {
    return (

      <div>
       <Header />
      <div>
       <Row>
           <Navigation />
       </Row>
       <Row>
         <h2 className='Contact-h2'>Contact Me</h2>
       </Row>
       <Row>
           <Bar
           color='darkblue'
           />
       </Row> 
       
      <Row>
        <Card className='Contact-Card'>
          <CardImg className='Contact-CardImg'   src={Hicks}/>
            <CardBody className='Contact-Body'>
               <CardTitle className='Contact-Title'>KatrinarHicks@Outlook.com</CardTitle>
           </CardBody>
                <CardText className='Contact-Text'>Cell: 330-348-1215</CardText>
               <CardText className='Contact-Text'>Home: 330-908-1367</CardText>
               <CardLink className='Contact-Link' href="https://github.com/Kathicks387">github.com/Kathicks387</CardLink>
               <CardLink className='Contact-Link2' href="https://www.linkedin.com/in/katrinahicks387/">Linkedin.com/in/katrinahicks387</CardLink>
              </Card>
      </Row>     
    
      <Footer />
      </div>
      </div>
    );
    }
   
 
export default Contact;