import React from 'react';
import { Container, Col, Row } from 'reactstrap';
import Header from '../Header/Header';
import Navigation from '../Navigation/Navigation';
import Bar from '../bar/Bar'
import Hicks from '../img/Hicks.jpg';
import ReactRoundedImage from 'react-rounded-image';
import Footer from '../footer/Footer';


const Home = () => {
    return (
      <div>
      <Header />
     <div>
      <Row>
          <Navigation />
      </Row>
      <Row>
         <h2 className='About-h2'>Welcome</h2>
       </Row> 
       <Row>
           <Bar
           color='darkblue'
           />
       </Row>  
       <Container> 
                <Row>
                    <Col className='About-column'>
                    <ReactRoundedImage className='About-img' 
                    image={Hicks} alt="portrait"
                    roundedColor="darkblue"
                    imageWidth="400"
                    imageHeight="400"
                    roundedSize="13"
                    hoverColor="tan"
                    />
                   
                    </Col>
                    <Col className='About-column'>
                    <p className="About-text">Welcome, and thank you for viewing my portfolio.  Contained within this portfolio are a few of the apps I created as a student of the Case Western Reserve Coding Bootcamp.  These projects are in beta form and I eagerly look forward to enhancing and improving them.  Please feel free to contact me at (330)348-1215 or KatrinarHicks@Outlook.com with any questions you may have.                
                    </p>
                    </Col>
                    </Row>
                   
                </Container>
    <Footer />
      </div>
      </div>
      );
      }

      export default Home;