import React from "react";
import { Container, Row, Col} from "react-bootstrap";
import HicksResume from './HicksResume.pdf';

function Resume(){
    return(
        
        <Container>
            <Row >
                <Col md={12}>           
                    <embed style={{height:'620px', width:'100%'}}src={HicksResume}></embed>
                </Col>
            </Row>

        </Container>
    )
}

export default Resume;
