import React from 'react';
import Header from '../Header/Header'; 
import Navigation from '../Navigation/Navigation';
import Footer from '../footer/Footer';
import Bar from '../bar/Bar';
import WoodDoor from '../img/WoodDoor.gif';
import Vinyld from '../img/Vinyld.gif';
import './Group.css'
import { Row } from 'reactstrap';
import {Card, CardText, CardBody, CardTitle, CardLink, CardImg} from 'reactstrap';


const Group = () => {
    return (
      
       <div>
       <Header />
       <div>
       <Row>
           <Navigation />
       </Row>
       <Row>
         <h2 className='Group-h2'>Group Projects</h2>
       </Row> 
       <Row>
           <Bar
           color='darkblue'
           />
       </Row> 
       <Row>
        <Card className='Group-Card'>
          <CardImg className='Group-CardImg'   src={WoodDoor}/>
            <CardBody className='Group-Body'>
               <CardTitle className='Group-Title'>Job Search</CardTitle>
           </CardBody>
               <CardText className='Group-Text'>As a recent graduate of a coding bootcamp, users will need an app that will successfully help them to gain employment.
Users are able to enter desired job they are looking for and location into our search. They will be presented with matching jobs in a list. Users are able to click the apply button to apply for any of the listed jobs - this will take them directly to the site to apply. Jobs on the site were pulled from Muse and GitHub Jobs APIs.</CardText>
               <CardLink className='Group-Link' href=" https://github.com/Kathicks387/Job-Search">github.com/Kathicks387/Job-Search</CardLink>
               <CardLink className='Group-Link' href=" https://kathicks387.github.io/Job-Search/">kathicks387.github.io/Job Search</CardLink>
              </Card>
      </Row> 

      <Row>
        <Card className='Group-Card'>
          <CardImg className='Group-CardImg'   src={Vinyld}/>
            <CardBody className='Group-Body'>
               <CardTitle className='Group-Title2'>Vinyld</CardTitle>
           </CardBody>
               <CardText className='Group-Text'>Need a handy place to catalog your music?  This app can be used for storing all the music you love. Links are available within the app to take you to video and, or audio recordings of your selection.  This app was made with Javascript, Express, Node, Html and CSS. </CardText>
               <CardLink className='Group-Link' href="https://github.com/johannsp/CWRUBC-Project-2-Team">github.com/johannsp/CWRUBC-Project-2-Team</CardLink>
               <CardLink className='Group-Link' href=" https://project-2-cwrubc-summer2020.herokuapp.com/collection.html">project-2-cwrubc-summer2020.herokuapp.com/collection.html</CardLink>
              </Card>
      </Row> 

       <Footer /> 
       </div>
       </div>
      
    );
   }

   export default Group;